import women from '../../comment/women.js'
import recommend from '../../components/recommend.vue'
export default {
	name:'women',
	components:{
		recommend
	},
	data() {
		return {
			rect:0,
			moreTitle:'下一个',
			rDatas:[],
			kinds:[
				['熟女','素人','人妻','美少女','女子校生'],
				['新番榜','人气榜','下载榜']],
			info:{}
		}
	},
	onPageScroll(e) {
		this.rect = e.scrollTop
	},
	onLoad(e) {
		console.log(e)
		this.handleDatas(e.id)
	},
	methods: {
		async handleDatas(target){
			let res = await women(target)
			this.rDatas = res.showavs
			this.info = res
		}
	}
}
